.intro {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  text-align: center;

  & h2 {
    width: 100%;
    margin-right: 58%;
    position: relative;
    z-index: 2;

    @include title;

    font-size: 45px;
    line-height: 45px;
    font-family: $font-nothing;
    font-weight: bold;
    margin-bottom: 30px;

    &::before {
      content: none;
    }

    &::after {
      content: none;
    }
  }

  & p {
    font-size: 1.2vw;
    line-height: 2vw;
  }
}

.intro__welcome {
  width: 45%;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: space-around;

  & img {
    width: 50%;
    max-width: 370px;
  }
}

.intro__block {
  margin-top: -170px;
  width: 50%;
  height: auto;

  display: flex;
  flex-direction: column;
  justify-content: flex-start;

  & img {
    margin: 0 auto;
    height: 65vh;
    max-height: 650px;
    width: auto;
  }
}

.intro__list {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;

  position: relative;
  box-sizing: border-box;
  overflow: hidden;
}

.intro__item {
  display: block;
  overflow: hidden;
  height: auto;
  min-width: 0;
  padding: 0;

  outline: none;
}

@media (max-width: $tablet-width) {
  .intro {
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;

    & h2 {
      width: auto;
      font-size: 40px;
      margin-bottom: 2vw;
      margin-top: 3vw;
      margin-right: 0;

    }

    & p {
      font-size: 18px;
      line-height: 25px;
    }
  }

  .intro__welcome {
    order: 3;
    width: 100%;

    & img {
      display: none;
      width: 30%;
    }

    & a {
      font-size: 0.8em;
      padding: 7px 30px;
      margin-bottom: 30px;
      margin-top: 0;
    }
  }

  .intro__block {
    width: 100%;
    margin-top: 0;
    justify-content: center;

    & img {
      display: inline-block;
      margin: 0 auto;
      width: 50vh;
      max-width: none;
      height: auto;
    }
  }
}

@media (max-width: $width-mobile) {
  .intro {
    & h2 {
      font-size: 35px;
      line-height: 32px;
      margin-bottom: 2vh;
      margin-top: 20px;
    }
  }

  .intro__welcome {
    & img {
      display: none;
    }
  }

  .intro__block {
    width: 100%;
    height: auto;

    & img {
      display: inline-block;
      margin: 0 auto;
      width: 60vw;
      max-width: none;
      height: auto;
    }
  }

  .intro__slider {
    max-height: none;
  }
}
