.about__block {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  & h2 {
    @include title;

    &::before {
      content: " ";
      display: inline-block;
      background-color: $title;
      height: 2px;
      width: 110px;
      vertical-align: middle;
      margin-top: 0.68em;
      margin-bottom: 0.7em;
      flex-grow: 0;
      flex-shrink: 0;
      margin-right: 30px;
    }

    &::after {
      content: " ";
      display: inline-block;
      background-color: $title;
      height: 2px;
      width: 110px;
      vertical-align: middle;
      margin-top: 0.68em;
      margin-bottom: 0.7em;
      flex-grow: 0;
      flex-shrink: 0;
      margin-left: 30px;
    }
  }

  & p {
    font-size: 1.2vw;
    line-height: 2vw;
    text-align: center;
    margin-right: 20px;
    margin-left: 20px;
  }
}

.about__photo {
  height: auto;
  margin: 0;
  display: flex;
  flex-direction: column;

  & .about__img {
    position: relative;
    display: inline-block;

    width: 100%;
    height: auto;
  }

  & figcaption {
    font-size: 0.8vw;
    line-height: 2vw;
    text-align: center;
  }
}

.about__text {
  position: relative;
  margin-bottom: 3%;

  &::before {
    position: absolute;
    content: " ";
    display: inline-block;
    background-color: $title;
    height: 2px;
    width: 50%;
    transform: translateX(-50%);

    left: 50%;
    bottom: -35px;
    vertical-align: middle;
    flex-grow: 0;
    flex-shrink: 0;
  }
}


@media (max-width: $tablet-width) {
  .about__block {
    & p {
      font-size: 18px;
      line-height: 30px;

      margin-bottom: 5.5px;
      text-align: center;

      margin-right: 20px;
      margin-left: 20px;
      width: 100%;
    }
  }

  .about__photo {
    & figcaption {
      font-size: 16px;
    }

    & .about__img {
      width: 70vw;
      height: auto;
    }
  }

  .about__text {
    &::before {
      bottom: -15px;
    }
  }
}

@media (max-width: $width-mobile) {
  .about__block {
    padding: 0;
    width: 100%;

    & p {
      font-size: 16px;
      line-height: 18px;
      min-width: 300px;
      margin-right: 0;
      margin-left: 0;
    }

    & h2 {
      font-size: 40px;
      margin-bottom: 0.5%;

      &::before {
        content: none;
      }

      &::after {
        content: none;
      }
    }
  }

  .about__photo {
    display: none;

    & figcaption {
      font-size: 16px;
    }

    & .about__img {
      width: 70vw;
      height: auto;
    }
  }
}
